import { useEffect } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';

import { useSetCartState } from './recoils/recoilCart';
import { useQuery } from './hooks/useQuery';

import GlobalStyle from './GlobalStyle';

import { Home } from './components/pages/Home';
import { ShoppingCart } from './components/pages/ShoppingCart';

import { CartItemType } from './types';
import { isEqual } from './utils';
import { PATH } from './constants';

export const App = () => {
  const { data: cart, loading, error } = useQuery<CartItemType[]>('/cart-items');
  const setCartState = useSetCartState();

  useEffect(() => {
    if (!cart) return;

    setCartState(cart);
  }, [cart, setCartState]);

  useEffect(() => {
    if (!cart) return;

    setCartState((prev) => {
      if (!isEqual(prev, cart)) return cart;

      //      Set     
      // prev  return   set   ===    false   
      // TODO      
      return prev;
    });
  }, [cart]);

  return (
    <>
      <GlobalStyle />
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Routes>
          <Route path={PATH.HOME} Component={Home} />
          <Route path={PATH.CART} Component={ShoppingCart} />
        </Routes>
      </BrowserRouter>
    </>
  );
};
